@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.landing{
    /* width: 100 vw; */
    height: 100vh;
    display: grid;
    grid-template-columns: minmax(0px, 1fr) 320px minmax(0px, 1fr);
    /* grid-template-columns: 1fr minmax(160px, 320px) 1fr; */
    /* grid-template-columns: 1fr 320px 1fr; */
}
.game{
    background-image: url(/static/media/StoneWall_TXR.53ad008e.png);
    /* height: 100vh; */
    background-color: aqua;
    background-size: contain;
    /* background-attachment: local; */
    overflow: hidden;/* auto for testing change to hidden  */
}


.hamburguer--menu{
    display: none;
    position: absolute;
    font-size: 50px;
    margin: 25px 0 0 259px;
}


@media screen and (max-width : 609px){
    
    .hamburguer--menu{
        display: inline;
    }

}
#player {

    position: absolute;
    margin: 0;
    width: 40px;
    height: 40px;
    background-color: black;
    color: crimson;
    overflow: hidden;
    z-index: 2;
}

#player>img {
    width: 48px;
} 
.platform {
    position: relative;
    z-index: 1;
}
.deathzone {
    z-index: 2;
    background-color: transparent;
    position: absolute;
    width: 320px;
    height: 50px;
    /* margin: 808px 0 0 0; */

}

.deathzone>div {
    overflow: hidden;
    width: 54px;
    height: 50px;
}

.deathzone img {
    width: 800%;
    margin: -241px 0 0 -246px;
    /* width: 653%;
    margin: -184px 0 0 -197px; */
}
.left--section {
    background-color: khaki;
    z-index: 2;
}

.outTitle{
    display: block;
    width: 142px;
    margin: 108px 0px 0px 67px;
    /* margin-top: minmax(0px, 108px); */
    /* margin-left: minmax(0px, 67px); */
    /* margin: 0; */
    font-size: 3rem;
}


.inTitle{
    display: none;
