.section--right {
    background-color: khaki;
    z-index: 2;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    /* justify-content: flex-end; */
}

/* aside {
    margin-right: 40px;
} */

.aside--top {
    width: 100%;
    height: -webkit-fit-content;
    height: fit-content;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}

.aside--top>* {
    /* margin: 0; */
    margin: 26px 50px;  
}

.aside--bottom {
    width: 100%;
    height: 50%; /* in case webkit doesnt work */
    height: -moz-fill-available;
    height: -webkit-fill-available;
    height: stretch; /* supposedly new name for fill-available */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

.aside--bottom>* {
    /* margin: 0; */
    margin: 26px 50px;
}

.modal {
    border-radius: 0%;

    display: none;
    position: absolute;

    background-color: brown;
    /* width: 490px;
    width: 48%;
    height: 50%;
    margin: 13% 27% 0 0; */

    width: min(75vw, 500px);
    height: 50vh;
    /* margin: 13vh 13vw; */
    margin: 13vh max(13vw, calc(calc(100vw - 500px) / 2));

    /* align-items: flex-start; */
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;

}

.icon {
    font-size: 68px;
}

@media screen and (max-width : 609px){
    
    .aside--top>* {
        display: none;
        /* margin: 0; */
        margin: 26px 16px;  
    }
    
    
    .aside--bottom>* {
        display: none;
        /* margin: 0; */
        margin: 26px 16px;
    }

}
